.main-header {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  min-height: 38px;
  margin: 10px auto 0 auto;

  &--margin-bottom {
    margin-bottom: 17px;
  }

  @media (min-width: @min-width-tablet) {
    .width-tablet;
    min-height: 51px;
    margin: 24px auto 64px auto;
  }

  @media (min-width: @min-width-desktop) {
    .width-desktop;
    min-height: 59px;
    margin: 53px auto 74px auto;
    padding: 0 50px;
  }
}

.main-header__logo {
  padding-left: 20px;

  @media (min-width: @min-width-tablet) {
    padding-left: 0;
  }
}

.main-header__logo-image {
  display: block;

  @media (min-width: @min-width-tablet) {
    display: block;
    width: 174px;
    height: auto;
  }

  @media (min-width: @min-width-desktop) {
    display: block;
    width: 202px;
    height: auto;
  }

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }
}

